.App {
  display: flex;
  flex-direction: column;
}
.divMain{
  background-color: #d8edf4
}
.buttonsAndData{
  display: flex;
  flex-direction: row;
  margin-left: 10%;
  margin-top: 4%;
  gap:40px;
}
.buttons{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 20%;
}
.button{
  background-color: transparent;
  padding: 10px 20px;
  text-align: left;
  font-family: FreeMono, monospace;
  font-size: 20px;
  color: rgb(88, 88, 193);
  border: none;
  border-radius: 5px;
}
.button:hover{
  background-color:rgb(88, 88, 193);
  color:black;
  cursor:pointer;
  transition: all 1s;
}
.button:focus{
  background-color:rgb(88, 88, 193);
  color:black;
}
.p-buttons{
  font-size: 25px;
  font-family: FreeMono, monospace;
}
.dataInput{
  display: flex;
  flex-direction: column;
  justify-content: start;
}
.h1Data{
  margin-top: 0;
  font-family: FreeMono, monospace;
  font-size: 40px;
}
.h1main{
  font-family: FreeMono, monospace;
  font-size:1.5em;
  color: rgb(168, 152, 152);
  margin-left: 10%;
}
.p-data{
  font-family: FreeMono, monospace;
  font-size: 20px;
}